<template>
<div id="app">
   <div class="mainBox">
     <div class="headbox">
      <div class="navbar">
           
<router-link to="/shiyan/zuoye" ><div class="item active"  >作业指导书</div></router-link>
<router-link to="/shiyan/baojia" ><div class="item" >报价管理</div></router-link>
<router-link to="/shiyan/moban" ><div class="item"  >报告模板</div></router-link>
<router-link to="/shiyan/reportGuanli" ><div class="item" >报告管理</div></router-link>
<router-link to="/shiyan/shiyanren" ><div class="item"  >试验人员资料</div></router-link>

</div> 
     </div>
         <div class="back" v-show='isshow'>
          
      <!-- <dianchangxiugai></dianchangxiugai> -->
      <div class="card1">
          <el-form ref="form" v-model="form.region" label-width="80px">
            <el-form-item label="电厂名称">
              
              <el-input v-model="form.name"></el-input>
            </el-form-item>
             <el-form-item label="上传附件">
              
             <div>
                     上传附件:<input type="file" class="file" id="btn1"> 

                    </div>
            </el-form-item>
             <el-form-item>
                          <el-button type="primary" @click='refrashData(),show()'> 保存</el-button>
                        </el-form-item>
          </el-form>
        </div>
    </div>
      <div class="bottombox">
          <div class="card2">
               <div class="card2_top">
                 <el-form :inline="true" :model="form" class="demo-form-inline">
                  <div style="float: left; margin-left:10%; margin-top: 20px;">
                      <el-form-item label="关键字" >
                          <el-input v-model="form.powerPlantName" placeholder="请输入关键字" id="btn0"></el-input>
                        </el-form-item>
                        <el-form-item>
                          <el-button type="primary" @click='search()'>查询</el-button>
                        </el-form-item>
                        <el-form-item>
                          <el-button type="info" @click='reback'>重置</el-button>
                        </el-form-item>
                  </div>
                   <div style="float: right; margin-right:10%; margin-top: 20px;">
                      <el-form-item>
                          <el-button type="success"><router-link to='./zuoye'> +新增</router-link></el-button>
                        </el-form-item>
                   </div>
                 </el-form>
                   
               </div>
                <div id="card2_bottom">
        <el-table :data="list.slice((currpage - 1) * pagesize, currpage * pagesize)" style="width: 100%"
          @selection-change="handleSelectionChange" >
          <el-table-column prop="guidingBookName" label="文档名称">
                   </el-table-column>
                   <el-table-column prop="guidingBookUrl" label="附件">
                   </el-table-column>
                   <el-table-column prop="uploadDate" label="上传时间">
                    </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button @click="handleClick(scope.row)" type="text" size="small"  >删除
              </el-button>
              <el-button type="text" size="small" @click='show(),handleClick2(scope.row)'>修改
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination background layout="prev, pager, next, sizes, total, jumper" :page-sizes="[5, 10, 15, 20]"
          :page-size="pagesize" :total="list.length" @current-change="handleCurrentChange"
          @size-change="handleSizeChange">
        </el-pagination>
      </div>
             
    </div>
      </div>
  </div>
   </div>





</template>

<script>
import axios from 'axios'
export default {
  name: '',
   created(){
         axios.get('http://localhost:8084/guidingbook/findall')
      
          .then(response => {

            // console.log(response)
            this.list = response.data.data.filter((item) => {
              return item.infoState == 0;
            })
            this.list2 = this.list;
           
          })


          .catch(error => {

            console.log(error);

            alert('网络错误，不能访问');

          })

    },
  data() { 
    return {
        isshow:false,
        
          form: {
            name: '',
            region: 'oqwkoeo',
  
          },
          formInline: {
            user: '',
            region: ''
          },
        guidingBookName:'',
        guidingBookUrl:'',
         guidingBookId:'',
        uploadDate:'',
        infoState:'',
        msg: 8888,
        list3:[

        ],
        list2:[

        ],
        list:[
         
          
        ],
        pagesize: 5,
        currpage: 1
      }
  },
  methods:{
     handleCurrentChange(cpage) {
        this.currpage = cpage;
      },
      handleSizeChange(psize) {
        this.pagesize = psize;
      },
      handleSelectionChange(val) {
        console.log(val)
      },
    //查询
      search() {
       var obtn1 = document.getElementById('btn0').value
       axios.get(`http://localhost:8084/guidingbook/findByName?guidingBookName=${obtn1}`).then( result => {
         console.log(result)
           this.list = result.data.data.filter(item =>{
             return item.infoState == 0;
           })
              this.list3 = this.list;
     
        
           
        } ).catch( ( error ) => {
          
        
            console.log(error);
            if (error.response) {
              
                console.log( error.response.data );
                console.log( error.response.status );
                console.log( error.response.headers );
            } else {
                
                console.log( "Error", error.message );
            }
            console.log( error.config );
        } );

      },


    //修改
    show(){
      this.isshow = !this.isshow;
     
    },
     //handleClick2()  函数实现修改数据记录变化后的数据
      handleClick2(a){
        alert(a.guidingBookName)
        this.form.name = a.guidingBookName;
        this.guidingBookId = a.guidingBookId;
        this.guidingBookUrl = a.guidingBookUrl;
        this.infoState = a.infoState;
        this.uploadDate = a.uploadDate;
      },

      refrashData(){
        // console.log(this.guidingBookId, this.form.name)
            axios.post('http://localhost:8084/guidingbook/update', {
          guidingBookId:this.guidingBookId,
          guidingBookName:this.form.name,
          infoState:this.infoState,
          guidingBookUrl:this.guidingBookUrl,
          uploadDate:this.uploadDate
          

            })
            .then(function (response) {
          
              
            })
            .catch(function (error) {
              console.log(error);
            });
           
        window.location.reload();
        alert('修改成功!')
       
      },



    //重置
      reback(){
        document.getElementById('btn0').value='';
        this.list = this.list2;
      },
       
      



    //删除
     //handleClick() 函数实现发送id，后台实现删除功能
      handleClick(a){
        alert(a.guidingBookId)
       
        axios.get(`http://localhost:8084/guidingbook/delete?guidingBookId=${a.guidingBookId}`).then( result => {
        
               alert('确认删除？')
             window.location.reload();
        } ).catch( ( error ) => {
          
            console.log(error);
            if (error.response) {
               
                console.log( error.response.data );
                console.log( error.response.status );
                console.log( error.response.headers );
            } else {
              
                console.log( "Error", error.message );
            }
            console.log( error.config );
        } );
         
      
      },
  },
  activated(){
      this.$router.push(this.path);   //处理默认现实的路由
    },
    beforeRouteLeave(to, from, next){
      this.path = this.$route.path;
      next();
    },
 }
</script>

<style lang="" scoped>
 .back{
    position: absolute;
    width: 100%;
    height: 100%;
    background: black;
    background-color:rgba(0,0,0,0.5);
    z-index: 2;
  }
  .el-input {
    width: 150px;
    height: 25px;
  }

  .el-select {
    width: 150px;
    height: 25px;
  }

  .el-form-item {
    margin-bottom: 10px;
  }

 .el-button {
    padding: 10px 20px;
  }


  .card2 {
    text-align: center;
    width: 97%;
    margin: auto;
    background: white;
  }

  .card1 {
    width: 550px;
    padding: 10px;
    margin: 100px auto;
    text-align: center;
    background: white;
  }
  .el-form-item__label{
    width: 100px !important;
    padding: 0 !important;
  }
  .card1 .el-input {
    width: 400px;
    height: 30px;
  }

  .card1 .el-select {
    width: 400px;
    height: 25px;
  }


  .card1 .el-button {
    padding: 8px 16px;
  }

  .element.style{
    width: 120px;
  }


        html,body{
  width: 100%;
  height: 100%;
}
  .mainBox{
    margin-top: 10px;
    width: 97%;
    height: 90%;
    margin-left: 1.5%;
    background: white;
    position: absolute;
    box-shadow: 0px 0px  1px black;
  }

  .bottombox{
    position: absolute;
    width: 100%;
    height: 100%;
    /* border: 2px solid red; */
  }
        
.navbar{
    width: 100%;
    /* margin-left: 1.5%; */
    height: 30px;
   
    display: flex;
    /* border: 2px solid red; */
    background: #F0F2F5;
}

.item.active{
 background:#409EFF;
color: white;
}

.item{
    margin-right: 10px;
    width: 85px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    color:black;
    font-size: 14px;
}
</style>


